import React, { Component, lazy, Suspense } from "react";
import Skeleton from "react-loading-skeleton";
const FormHistoryUser = lazy(() => import("./Components/FormHistoryUser"));

class HistoryUser extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Cetak History User
                </h4>
                <div className="col-lg-12 col-md-12">
                  <Suspense
                    fallback={<Skeleton count={1} width="100%" height={80} />}
                  >
                    <FormHistoryUser />
                  </Suspense>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default HistoryUser;
